import http from '../../../../configs/httpConfig'
import {notifyError, notifySuccesslogin} from "../../../Toast"
import useJwt from '../../../../@core/auth/jwt/useJwt'
const config = useJwt.jwtConfig

export const signIn = (params) => {
 return async (dispatch) => {
  
    await http.post('/admin_user/login', params).then((response) => {
      console.log(response)
      notifySuccesslogin(response.data.message)
      localStorage.setItem(
        'TOKEN',
        `${response.data.data.token_type} ${response.data.data.access_token}`
      )
      dispatch({ type: 'LOGIN_SUCCESS' })
    }).catch((err) => {
      notifyError(err.response.data.message)
    })
  }
}

export const handleLogin = (data) => {
  console.log(data)
  return (dispatch) => {
    dispatch({
      type: 'LOGIN_SUCCESS',
      data,
      config,
      [config.storageTokenKeyName]: data[config.storageTokenKeyName],
      [config.storageRefreshTokenKeyName]:
        data[config.storageRefreshTokenKeyName]
    })

    // ** Add to user, accessToken & refreshToken to localStorage
    localStorage.setItem('userData', JSON.stringify(data))
    console.log(data)
    localStorage.setItem(
      config.storageTokenKeyName,
      JSON.stringify(data.accessToken),
      console.log(data.accessToken)
    )
    localStorage.setItem(
      config.storageRefreshTokenKeyName,
      JSON.stringify(data.refreshToken)
    )
  }
}
