import React, { useEffect} from 'react'
import { useSkin } from '@hooks/useSkin'
//import { toast } from 'react-toastify'
//import Avatar from '@components/avatar'
//import { Check } from 'react-feather'
import { useDispatch, useSelector } from 'react-redux'
import { Link, useHistory } from 'react-router-dom'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
// import log from '../../assets/images/backgrounds/Group 65114.png'
import {
  Row,
  Col,
  CardTitle,
  CardText,
  Form,
  FormGroup,
  Label,
  Input,
  CustomInput,
  Button,
  FormFeedback
} from 'reactstrap'
import '@styles/base/pages/page-auth.scss'
import './index.scss'
import { ReactComponent as LogoIcon } from '@src/assets/images/logo/logo.svg'
import { LoginSchema } from './validations'
import { signIn } from './store/actions'
const Login = () => {
  const [skin] = useSkin()
  const dispatch = useDispatch(),
  store = useSelector((state) => state.auth)
  const { isLoggedIn } = store
  //const { isLoggedfaill} = store
  const history = useHistory()
  useEffect(() => {
    const token = localStorage.getItem('TOKEN') 
    console.log(localStorage)
  if (token) {
    console.log(token)
    history.push("/home")
  }   
 }, [isLoggedIn])
  
 const illustration =
     skin === 'white' ? 'Web 1920 â€“ 1.png' : 'Web 1920 â€“ 1.png',
   source = require(`@src/assets/images/backgrounds/${illustration}`).default
    const { register, errors, handleSubmit } = useForm({
      mode: 'onChange',
      resolver: yupResolver(LoginSchema)
    }) 


  const onSubmit = (data) => {
    console.log(data)
    dispatch(signIn(data))

  }

  return (
    <div className="auth-wrapper auth-v2">
      <Row className="auth-inner m-0">
        <Link className="brand-logo" to="/">
          <LogoIcon className="logo" />
          <h2 className="brand-text text-primary ml-1">Sciflare Fantasy</h2>
        </Link>
        <Col className="d-none d-lg-flex align-items-center p-5" lg="8" sm="12">
          <div className="w-100 d-lg-flex align-items-center justify-content-center px-5 ">
            <img className="img-fluid" src={source} alt="Login V2" />
          </div>
        </Col>
        <Col
          className="d-flex align-items-center auth-bg px-2 p-lg-5"
          lg="4"
          sm="12"
        >
          <Col className="px-xl-2 mx-auto" sm="8" md="6" lg="12">
            <CardTitle tag="h2" className="font-weight-bold mb-1">
              Welcome to Sciflare Fantasy! ðŸ‘‹
            </CardTitle>
            <CardText className="mb-2">
              Please sign-in to your account and start the adventure
            </CardText>
            <Form
              className="auth-login-form mt-2"
              onSubmit={handleSubmit(onSubmit)}
            >
              <FormGroup>
                <Label className="form-label" for="email_id"> 
                  Email
                </Label>
                <Input
                  type="email"
                  name="email_id"
                  id="email_id"
                  innerRef={register({ required: true })}
                  invalid={errors && errors.email_id && true}
                  placeholder="Enter your email"
                />
                {errors && errors.email_id && (
                  
                  <FormFeedback>{errors.email_id?.message}</FormFeedback>
                )}
              </FormGroup>
              <FormGroup>
                
                  <Label className="form-label" for="password">
                    Password
                  </Label>
                <Input
                  type="password"
                  id="password"
                  name="password"
                  innerRef={register({ required: true })}
                  className="input-group-merge"
                  invalid={errors && errors.password && true}
                  placeholder="Enter your password"
                />
                {errors && errors.password && (
                  <FormFeedback>{errors.password?.message}</FormFeedback>
                )}
              </FormGroup>
              <FormGroup>
                <CustomInput
                  type="checkbox"
                  className="custom-control-Primary"
                  id="remember-me"
                  label="Remember Me"
                />
              </FormGroup>
              <Button.Ripple type="submit" color="primary" block>
                Sign in
              </Button.Ripple>
            </Form>
            <p className="text-center mt-2">
              <span className="mr-25">New on our platform?</span>
              <Link to="/">
                <span>Create an account</span>
              </Link>
            </p>
          </Col>
        </Col>
      </Row>
    </div>
  )
}

export default Login
